
.loww{
  height: 60px;
}
.img{
  display:-webkit-box;
  display:-webkit-flex;
  display:flex;
	-webkit-box-align:center;
	-webkit-align-items:center;
	        align-items:center; 
	-webkit-box-pack:center; 
	-webkit-justify-content:center; 
	        justify-content:center;
}
.img img{
  width:100%;
	height:auto;
}
.head{
  height: 80px;
  width: 100%;
  margin-top:3px; 
  overflow:hidden
}
.left{
  width: 50%;
  float: left;
}
.right{
  border-radius: 5px;
  background: rgb(100, 176, 238);
  width: 40%;
  height: 40px;
  float: left;
  text-align: center;
  line-height: 40px;
  margin: 3% auto;
  color: #fff;
}
.a {
  font-size: 23px;
  color: red;
}
.b{
  font-size: 16px;
  color: #999;
}
.right button{
  border-radius: 5px;
  margin-top:10px; 
  width: 150px;
  background: rgb(100, 176, 238);
  color: #fff;
  line-height: 36px;
  font-size: 20px;
}
.mid {
  overflow: hidden;
  line-height: 40px;
  width: 100%;
  height: 40px;
}
.mid a{
  color: #000;
  margin-right:20px;
}
.low{
  background:  rgb(100, 176, 238);
  color:#fff;
  font-size: 15px;
  width: 100%;
  height: 60px;
  text-align: center;
  position: fixed;
  bottom:0;
}
.mid .active{
  color: blue;
}



