
.tp .wz {
  font-size: 12px;
  position: absolute;
  bottom: 0;
  left: 35%;
}
.tp {
  overflow: hidden;
  margin-top: 10px;
}
.tp div {
  border: 1px solid #000;
  float: left;
  width: 31%;
  height: 55px;
  margin-left: 1%;
  margin-right: 0.6%;
  margin-top: 1%;
  position: relative;
}
.topa {
  overflow: hidden;
  height: 40px;
}
.topa div {
  text-align: center;
  border: 1px solid #cdcdcd;
}
.lefta {
  font-size: 20px;
  line-height: 40px;
  width: 48%;
  float: left;
  height: 40px;
}
.righta {
  font-size: 20px;
  line-height: 40px;
  height: 40px;
}



